import "./styles.css";

//function to display the names
function showNames(person) {
  return (
    <div>
      {person.FirstName +
        " " +
        person.LastName +
        ", " +
        "Favorite Color: " +
        person.Color}
    </div>
  );
}
//array of people
let people = [
  { FirstName: "Viktor", LastName: "Lymar", Color: "Green" },
  { FirstName: "Traci", LastName: "Siebel", Color: "Purple" },
  { FirstName: "Ida", LastName: "Voong", Color: "Blue" },
  { FirstName: "Ana", LastName: "Rivera", Color: "Green" },
  { FirstName: "Aiden", LastName: "Fuller", Color: "Red" },
  { FirstName: "Kyle", LastName: "Wan", Color: "Yeah" },
  { FirstName: "Enjun", LastName: "Li", Color: "Black" },
  { FirstName: "Luis", LastName: "Garcia", Color: "Black" },
  { FirstName: "Amanda", LastName: "Yu", Color: "Blue" },
  { FirstName: "Yifei", LastName: "Du", Color: "Black" },
  { FirstName: "Hector", LastName: "Del Valle", Color: "Gray" },
  { FirstName: "HaoYu", LastName: "Lin", Color: "Red" }
];

//code to show elements on screen
let elements = people.map(showNames);
